import React, { useEffect, useState } from 'react';
import './OrderDetail.css';
import { Link } from 'react-router-dom';
import orderAPI from '../../apis/orderAPI';
import formatCurrency from 'format-currency';

const OrderDetail = (props) => {
  const [order, setOrder] = useState({});
  const [products, setProducts] = useState([]);
  useEffect(() => {
    orderAPI.getOrderDetailByCode(props.match.params.code).then((res) => {
      setOrder(res.data.data.order);
      setProducts(res.data.data.products);
      // console.log(res.data.data);
      // console.log(res.data.data.order);
    }).catch((err) => {
      console.log(err);
    })

  }, [props.match.params.code]);
  return (
    <>
      <section className="breadcrumbbar">
        <div className="container">
          <ol className="breadcrumb mb-0 p-0 bg-transparent">
            <li className="breadcrumb-item"><Link to="/" >Trang ch</Link></li>
            <li className="breadcrumb-item"><Link to="/orders/history">Qun l n hng</Link></li>
            <li className="breadcrumb-item active"><a href="# " >Chi tit n hng #{props.match.params.code}</a></li>
          </ol>
        </div>
      </section>
      <section className="content">
        <div className="container">
          <div className="row">
            <div className="col-4 col-md-4">
              <div className="title">A CH NGI NHN</div>
              <div className="content-receiver">
                <h6>{order.o_nameReceiver}</h6>
                <p>a ch: {order.o_shippingAddress}</p>
                <p>S in thoi: {order.o_phoneReceiver} </p>
              </div>
            </div>
            <div className="col-4 col-md-4">
              <div className="title">HNH THC GIAO HNG</div>
              <div className="content-receiver">
                <p> {order.o_shippingFee  === 15000 ? 'Giao hng chun' : 'Giao hng nhanh (2h-3h)'} </p>
              </div>
            </div>
            <div className="col-4 col-md-4">
              <div className="title">HNH THC THANH TON</div>
              <div className="content-receiver">
                <p>{order.o_payment === 'pay-cash' ? 'Thanh ton bng tin mt khi nhn hng' : 'Thanh ton bng th'}</p>
              </div>
            </div>
          </div>

          <div className="row">
            <div className="col-12">
              <div className="content-product-order">
                <table className="table table-hover">
                  <thead>
                    <tr>
                      <th>Sn phm</th>
                      <th>Hnh nh</th>
                      <th>Gi</th>
                      <th>S lng</th>
                      <th>Tm tnh</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      products.map((v, i) => {
                        return (
                          <tr key={i}>
                            <td>{v.product.p_name}</td>
                            <td>
                              <img src={ v.product.p_image_detail.url } alt="product" style={{ height: '100px' }} />
                            </td>
                            <td>{formatCurrency(v.product.p_price)} </td>
                            <td>{v.quantity}</td>
                            <td>{formatCurrency(v.price)} </td>
                          </tr>
                        )
                      })
                    }
                  </tbody>
                  <tfoot>
                    <tr>
                      <td colSpan="4"><span>Tm tnh</span></td>
                      <td>{formatCurrency(order.o_totalPrice - order.o_shippingFee) } </td>
                    </tr>
                    <tr>
                      <td colSpan="4"><span>Gim gi</span></td>
                      <td>0 </td>
                    </tr>
                    <tr>
                      <td colSpan="4"><span>Ph vn chuyn</span></td>
                      <td>{formatCurrency(order.o_shippingFee)} </td>
                    </tr>
                    <tr>
                      <td colSpan="4"><span>Tng cng</span></td>
                      <td>{ formatCurrency(order.o_totalPrice)} </td>
                    </tr>
                  </tfoot>
                </table>
              </div>
            </div>
          </div>
          
          <div className="row">
            <div className="col-12 py-2">
                {
                  order.o_status === ' hy' ? (<button className="btn" style={{ background: '#F5A623', minWidth: '200px' }} >n hng  hy</button>) :
                   (<Link to={`/orders/tracking/${props.match.params.code}`} className="btn" style={{ background: '#F5A623', minWidth: '200px' }}>Theo di n hng</Link>)
                }
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default OrderDetail
