import React from 'react';
import filterAPI from '../../apis/filterAPI';
import useFullPageLoader from './../../hooks/useFullPageLoader';

const FilterPrice = (props) => {
  const [loader, showLoader, hideLoader] = useFullPageLoader();

  let handleClick = (range) => {
    showLoader();
    filterAPI.filterPrice(props.cateId, { range: range }).then(res => {
      hideLoader();
      props.handleFilter(res.data.data);
    });
  }

  return (
    <div className="item-filter">
      <h6>GI</h6>
      <div>
        <div><span className="badge" onClick={() => handleClick("max-50")} > Di 50.000 </span></div>
        <div><span className="badge" onClick={() => handleClick("from-50-to-150")} > T 50.000  n 150.000 </span></div>
        <div><span className="badge" onClick={() => handleClick("min-150")} > Trn 150.000 </span></div>
      </div>
      <hr />
      { loader}
    </div>
  )
}

export default FilterPrice;
